</template>
<script>
import {data} from "../assets/videoData.js"
export default {
  name: "mobile",
  data () {
    return {
      data
    }
  },
  methods: {
    toDetail (item1) {
      sessionStorage.setItem("detailData", JSON.stringify(item1))
      this.$router.push("/detail")
    }
  }
}
</script>
