<template>
  <div>
    <div id="video">
      <video :src="currentSrc" controls autoplay></video>
    </div>
  </div>
</template>
<script>
export default {
  name: "play",
  data () {
    return {
      currentSrc: ""
    }
  },
  mounted () {
    this.currentSrc = this.$route.query.url
  }
}
</script>
<style scoped>
#video {
  width: 85vw;
  margin: 0 auto
}
video {
  width: 100%;
  height: 100%;
}
</style>