<template>
  <div>
    <div id="title">{{data.name}}—{{videoList[curIdx].name}}</div>
    <div id="video">
      <video :src="curSrc" controls></video>
    </div>
    <div id="video-list">
      <div
        :class="'video-name '+(curIdx==idx?'active':'')"
        v-for="(item,idx) in videoList"
        :key="item.name"
        @click="changeVideo(item.url,idx)"
      >
        <span>{{item.name}}</span>
        <svg
          t="1581561060045"
          class="icon"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="2077"
          width="24"
          height="24"
        >
          <path
            d="M692.4 487.3L412.9 316.9c-19.3-11.8-44 2.1-44 24.7v340.7c0 22.6 24.7 36.5 44 24.7l279.6-170.3c18.5-11.3 18.5-38.1-0.1-49.4z"
            fill="#4EEAAB"
            p-id="2078"
          />
          <path
            d="M512 0C229.2 0 0 229.2 0 512s229.2 512 512 512 512-229.2 512-512S794.8 0 512 0z m0 947.2C272 947.2 76.8 752 76.8 512S272 76.8 512 76.8 947.2 272 947.2 512 752 947.2 512 947.2z"
            fill="#454563"
            p-id="2079"
          />
          <path
            d="M692.4 487.3L412.9 316.9c-4.8-2.9-10-4.3-15-4.3-15.1 0-29 12-29 29v340.7c0 16.9 13.9 29 29 29 5 0 10.2-1.3 15-4.3l279.6-170.3c18.5-11.3 18.5-38.1-0.1-49.4zM445.7 597.1V426.9L585.4 512l-139.7 85.1z"
            fill="#454563"
            p-id="2080"
          />
        </svg>
      </div>
    </div>
  </div>
</template>
<script>
// const data = JSON.parse(sessionStorage.getItem("detailData"))
// let videoList = []
// function formatVideoList (arr, surpIdx) {
//   arr.map((item, inneridx) => {
//     item.name = (surpIdx ? surpIdx : "") + (inneridx + 1 + ".") + item.name
//     videoList.push(item)
//     if (item.children.length > 0) {
//       formatVideoList(item.children, (surpIdx ? surpIdx + "." : "") + (inneridx + 1 + "."))
//     }
//   })
// }
// formatVideoList(data.children, 0)
export default {
  data () {
    return {
      data: {},
      curSrc: "",
      videoList: [],
      curIdx: 0
    }
  },
  mounted () {
    this.data = JSON.parse(sessionStorage.getItem("detailData"))
    this.formatVideoList(this.data.children, 0)
    this.curSrc = this.videoList[0].url
  },
  methods: {
    formatVideoList (arr, surpIdx) {
      arr.map((item, inneridx) => {
        item.name = (surpIdx ? surpIdx : "") + (inneridx + 1 + ".") + item.name
        this.videoList.push(item)
        if (item.children.length > 0) {
          this.formatVideoList(item.children, (surpIdx ? surpIdx : "") + (inneridx + 1 + "."))
        }
      })
    },
    changeVideo (url, idx) {
      this.curSrc = url
      this.curIdx = idx
    }
  }
}
</script>
<style scoped>
* {
  box-sizing: border-box;
}
#title {
  background-color: #409eff;
  text-align: center;
  height: 10vw;
  line-height: 10vw;
  font-size: 4vw;
  color: #fff;
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
  white-space: nowrap;
  padding: 0 2vw;
}
#video {
  width: 100vw;
  margin: 0 auto;
  margin-bottom: 2vw;
}
video {
  width: 100%;
  height: 100%;
}
.video-name {
  width: 96vw;
  height: 12vw;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2vw 0;
  margin: 0 2vw;
  border-bottom: 1px solid #ccc;
}
.video-name.active {
  color: #409eff;
  font-weight: bold;
}
</style>