<script>
import { data } from "../assets/videoData.js"
let switchs = {}
function fromSwitchs (arr) {
  if (arr.length > 0) {
    arr.map((item) => {
      switchs[item.id] = false
      fromSwitchs(item.children)
    })
  }
}
fromSwitchs(data)
switchs[1000]=true
switchs[2000]=true
switchs[3000]=true
export default {
  name: 'pc',
  data () {
    return {
      data,
      switchs: switchs
    }
  },
  mounted () {
    // this.$nextTick(() => {
    //   this.fromSwitchs(data)
    // })
    if (document.body.clientWidth <= 900) {
      this.$router.replace("/mobile")
    }
  },
  methods: {
    changeSwitch (id) {
      this.switchs[id] = !this.switchs[id]
    },
    play (url) {
      window.open(location.origin + "/#/play?url=" + encodeURIComponent(url))
    },
    formatClass (idx, arr) {
      if (arr.length === 1) {
        return "tree single"
      } else if (idx === 0) {
        return "tree first"
      } else if (idx === arr.length - 1) {
        return "tree last"
      } else {
        return "tree"
      }
    },
    fromSwitchs (arr) {
      if (arr.length > 0) {
        arr.map((item) => {
          this.switchs[item.id] = false
          this.fromSwitchs(item.children)
        })
      }
    }
  }
}

</script>
